.myButton{
  color: #fff;
  padding:10px 18px;
  border-radius: 5px; 
  font-size: 12px;
  font-weight: bold;
  transition: 0.3s;
}

.large{
   padding:10px 30px !important;
   min-width: 200px;
}
.myButton.primary{
   border-color:  #74632C;
  background: linear-gradient(to left, #c2a54a, #74632C); 
background: -webkit-linear-gradient(to right, #c2a54a, #ae9442, #74632C);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #c2a54a, #ae9442, #74632C); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.myButton.primary:hover{
    border-color: #fff;
  background: transparent
}

.myButton.outline{
   border-color: #fff;
  background: transparent
}

.myButton.outline:hover{
  border-color:  #74632C;
  background: linear-gradient(to left, #c2a54a, #74632C); 
background: -webkit-linear-gradient(to right, #c2a54a, #ae9442, #74632C);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #c2a54a, #ae9442, #74632C); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.myButton.dark{
   border:1px solid #000;
  background: #000;
  color:#fff
}

.myButton.dark:hover{
   border-color: #fff;
  background: transparent
}

.myButton.dark.dark-hover:hover{
   border-color: #000;
  background: transparent;
  color:#000
}



