.item-outer {
  display: block;
  float: left;
  height: 84px; /* Make this what you want */
  width: 100%; /* For 5 logos */
  /* Adjust margin and padding to taste */
  
  margin-left: 0;
  margin-right: 0;
}
.item-inner {
  display: block;
  height: 100%;
  width: 100%;
  position: relative; /* Needed for absolute positioning of images inside */
}
.item-inner img {
  width: auto;
  max-width: 100%;
  height: auto;
  max-height: 100%;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  filter: grayscale(100%);
}


.link{
  transition: 0.3s;
