.paging-item.active .paging-dot {
    height: 10px !important;
    width: 10px !important;
    border: 2px solid #c2a54a !important;
    border-radius: 50%;
    display: inline-block;
    cursor: pointer;
    background: red !important;
    fill: red !important;
}
.paging-item .paging-dot {
    height: 10px !important;
    width: 10px !important;
    border: 2px solid #000 !important;
    border-radius: 50%;
