    cursor: pointer;
    background: blue !important;
}


.scroller-to-top{
  transition: 0.3s;
  cursor: pointer;
  position: fixed;
    bottom: 20px;
    right: 20px;
    display: flex;
    flex-direction: column;
    height: 40px;
    width: 40px;
    border: 1px solid rgb(255, 255, 255);
    border-radius: 20px;
     background: linear-gradient(to left, #c2a54a, #74632C); 
    background: -webkit-linear-gradient(to left, #c2a54a, #ae9442, #74632C);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #c2a54a, #ae9442, #74632C); 
}

.scroller-to-top:hover{
  transform: scale(1.1);
}



